import * as Yup from "yup";

export default Yup.object().shape({
  name: Yup.string()
    .min(5, "O minimo de caracteres permitido so 5.")
    .required("O Preenchimento do Nome  obrigatrio!"),
  email: Yup.string()
    .email("Insira um formato de e-mail vlido!")
    .min(10, "O minimo de caracteres permitido so 10.")
    .required("O Preenchimento do Email  obrigatrio!"),
  password: Yup.string()
    .min(5, "O minimo de caracteres permitido so 5.")
    .required("Insira uma senha!"),
});
